/* import $ from 'jquery' */
  
export default {
	data() {
		return {
			user:{},
			usnerinfo:{}
		};
	},
	onLoad() {
		   // 监听事件  
		    uni.$on('appLogin',(usnerinfo)=>{  
		        this.usnerinfo = usnerinfo;  
				console.log("usnerinfo =",this.usnerinfo);
		    })  
		
	},
	onShow: function() {
		console.log(this.commonData.data);
		this.user = Object.assign(this.user, this.commonData.data.user);
		console.log("use =",this.user);

	},
	methods: {
		changeSkin(){
			uni.navigateTo({
					url: 'userInfo/userInfo?user='+ encodeURIComponent(JSON.stringify(this.user))
			});
		},
		
		login(){
			uni.showModal({
			    title: '是否跳转登录界面？',
			    success: function (res) {
			        if (res.confirm) {
			           uni.navigateTo({
			           		url: '../index/login'
			           });
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
		}

	}
}
